body {
  font-family: "Poppins";
  background-color: #fbfbfb;
}

/* Banner CSS */

.bg-cover {
  background-image: linear-gradient(
      to bottom,
      rgba(157, 14, 14, 0) 2%,
      rgba(79, 7, 7, 0.8) 100%
    ),
    url("../../public/images/cover.png");
  width: 100%;
  height: 400px;
  background-position: center;
  background-size: cover;
}

.circle_m {
  background-color: #fff;
  width: 100px;
  height: 100px;
  border-radius: 50px;
}

.logo_m {
  font-size: 60px;
  text-align: center;
  padding: 12px;
  font-weight: bold;
}

.line {
  font-weight: bold;
}

.input1 {
  border-radius: inherit;
  font-size: 12px;
  color: #636f88;
  /* border-radius: 10px; */
}

.selectbar {
  max-width: 18%;
  /* border-radius: 2px; */
}

.input2 {
  border-radius: inherit;
  font-size: 12px;
  color: #636f88;
  background-color: #fff;
  border-color: #fff;
  /* border-radius: 3px; */
}

.searchbar {
  /* border-radius: 3px; */
  max-width: 33%;
}

/* Quick Searches CSS */

.heading {
  font-weight: bolder;
  color: #192f60;
}

.subheading {
  color: #8c96ab;
  font-size: 16px;
}

.img-qs-m {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.box {
  margin-right: 40px;
  box-shadow: 2px 4px 3px #cecdcd;
}

.l-box {
  height: 151px;
  width: 150px;
}

.r-box {
  height: 140px;
  width: 180px;
  padding: 22px 22px;
}

.card-title {
  font-size: 20px;
  color: #192f60;
  font-weight: bold;
}

.card-content {
  color: #8c96ab;
  font-size: 12px;
  padding-top: 10px;
}

/* Suggestion Box */

.suggestionBox {
  background-color: white;
  margin-top: 2px;
  display: flex;
  flex-direction: column;
  position: absolute;
  margin: 37px 12px 0px 0px;
  width: 22rem;
}

.suggList {
  list-style-type: none;
}

.suggImg {
  height: 55px;
  border-radius: 50%;
  width: 55px;
  display: initial;
  margin: 6px 5px 6px -26px;
}

.suggName {
  color: #192f60;
  font-size: 11px;
  font-weight: 800;
}

.suggLoc {
  color: #192f60;
  font-size: 10px;
  display: block;
  margin: -25px 0px 8px 37px;
}

/* Payment Card */
.payCard {
  height: 5em;
  width: 20em;
  padding: 20px 0px 0px 8px;
}
